<script scoped>
  export default{
    methods:{
      showMessageDefault(){
        this.$wmessage.show();
      },
      showMessageNormal(){
        this.$wmessage.show({
          type: 'normal',
          info: '',
          sure: function(){
            console.log('!');
          }
        });
      },
      showMessageWarning(){
        this.$wmessage.show({
          type: 'warning',
          info: '',
          sure: function(){
            console.log('!');
          },
          cancel: function(){
            console.log('');
          }
        });
      },
      showMessageAnomalous(){
        this.$wmessage.show({
          type: 'anomalous',
          info: ''
        });
      },
      showMessageError(){
        this.$wmessage.show({
          type: 'error',
          info: ''
        });
      }
    }
  }
</script>


